import React, { Component } from "react";
import Header from "parts/Header";
import Hero from "parts/Hero";

type Props = {
  history: object;
};

type State = {};

export default class MyOwn3D extends Component<Props, State> {
  state = {};

  render() {
    return (
      <>
        <Header {...this.props} />
        <Hero />
        <div className="container px-4 pt-4">
          <p className="whitespace-nowrap">my own 3d</p>
        </div>
      </>
    );
  }
}
