import React, { Component} from "react";
import Header from "parts/Header";
import Hero from "parts/Hero";


type Props = {
  history: object;
};

type State = {};

export default class Home extends Component<Props, State> {
  state = {};

  render() {
    return (
      <>
        <Header {...this.props} />
        <Hero />
      </>
    );
  }
}
