import React from "react";
import Brand from "parts/Brand";
import Button from "elements/Button";
import IconCompany from "assets/icons/icon_company.svg";
import IconProduct from "assets/icons/icon_product.svg";
/* eslint-disable */

export default function Header({ history }: any) {
  const getNavLink = (path: any) => {
    return history.location.pathname === path ? " active" : "";
  };

  const list = {
    company: [
      {
        name: "Home",
        link: `${process.env.REACT_APP_PATH_PREFIX}`,
      },
      {
        name: "My Avatar",
        link: `${process.env.REACT_APP_PATH_PREFIX}/pages/my-avatar`,
      },
      {
        name: "My Own 3D",
        link: `${process.env.REACT_APP_PATH_PREFIX}/pages/own-3d`,
      },
      {
        name: "Contact",
        link: `${process.env.REACT_APP_PATH_PREFIX}/pages/contact`,
      },
    ],
    product: [
      {
        name: "Elperado",
        link: `${process.env.REACT_APP_PATH_PREFIX}/pages/elperado`,
      },
      {
        name: "Box",
        link: `${process.env.REACT_APP_PATH_PREFIX}/pages/box`,
      },
      {
        name: "Blur Radars",
        link: `${process.env.REACT_APP_PATH_PREFIX}/pages/blur-radars`,
      },
      {
        name: "Enx",
        link: `${process.env.REACT_APP_PATH_PREFIX}/pages/enx`,
      },
    ],
  };
  

  return (
    <header
      className="border-b-[1px] border-solid border-gray-200 text-zinc-500 
      dark:border-gray-50 dark:text-white dark:bg-zinc-800"
    >
      <nav className="flex items-center justify-between max-w-6xl mx-auto px-4 py-4">
        <div className="flex-grow">
          <Brand className="font-semibold text-lg text-zinc-800 dark:text-white" />
        </div>
        {/* Dekstop Menus */}
        <ul className="flex-grow flex justify-end xs:hidden">
          {list.company.map((val, index) => {
            return (
              <li className={getNavLink(`${val.link}`)} key={`list-${index}`}>
                <Button
                  href={val.link}
                  type="link"
                  className="text-sm font-semibold py-1 px-2.5 ml-4 hover:text-black dark:text-white"
                  isBlock
                >
                  {val.name}
                </Button>
              </li>
            );
          })}
        </ul>

        {/* Mobile Menus */}
        <div className="flex flex-row relative group">
          <div className="burger-menu sm:hidden lg:hidden md:hidden xs:pr-2">
            <div className="border-[1.5px] border-[#27272A] dark:border-white  px-[7.5px] py-[2.5px] rounded-md ">
              <div className="group cursor-pointer">
                <div className="burger-strip group-hover:translate-y-[6px]"></div>
                <div className="burger-strip group-hover:opacity-0"></div>
                <div className="burger-strip group-hover:-translate-y-[6px] "></div>
              </div>
            </div>
          </div>

          <ul className="flex flex-col sm:hidden lg:hidden md:hidden absolute right-0 top-10 bg-zinc-50  p-4 rounded-lg border scale-0 group-hover:scale-100 origin-top-right transition z-[999]">
            <div className="flex">
              <div className="flex flex-col">
                <span className="text-md font-bold pt-1 pb-3 px-2.5 select-none dark:text-zinc-800 flex items-center">
                  <img src={IconCompany} alt="" className="w-4" />
                  <p className="ml-2.5 text-zinc-800">Company</p>
                </span>
                <div className="ml-3">
                  {list.company.map((val, index) => {
                    return (
                      <li
                        className={getNavLink(`${val.link}`)}
                        key={`list-${index}`}
                      >
                        <Button
                          href={val.link}
                          type="link"
                          className="text-sm whitespace-nowrap pb-3.5 px-2.5 ml-4 sm:hover:text-black  dark:text-zinc-500 "
                          isBlock
                        >
                          {val.name}
                        </Button>
                      </li>
                    );
                  })}
                </div>
              </div>
              <div className="flex flex-col">
                <span className="text-md font-bold  pt-1 pb-3 px-2.5 select-none dark:text-zinc-800 flex items-center">
                  <img src={IconProduct} alt="" className="w-4" />
                  <p className="ml-2.5 text-zinc-800">Products</p>
                </span>
                <div className="ml-3">
                  {list.product.map((val, index) => {
                    return (
                      <li
                        className={getNavLink(`${val.link}`)}
                        key={`list-${index}`}
                      >
                        <Button
                          href={val.link}
                          type="link"
                          className="text-sm whitespace-nowrap pb-3.5 px-2.5 ml-4 sm:hover:text-black  dark:text-zinc-500"
                          isBlock
                        >
                          {val.name}
                        </Button>
                      </li>
                    );
                  })}
                </div>
              </div>
            </div>
          </ul>
        </div>
      </nav>
      <div></div>
      <ul className="flex items-center max-w-6xl mx-auto px-4 pt-2 pb-4 text-sm space-x-6">
        <li>Explore</li>
        <li>Jouney</li>
        <li>Photo</li>
        <li>Graphics</li>
        <li>UI/X</li>
      </ul>
    </header>
  );
}
