import { BrowserRouter, Routes, Route } from "react-router-dom";
import { createBrowserHistory } from "history";

import Home from "./pages/Home";
import MyAvatar from "./pages/MyAvatar";
import MyOwn3D from "./pages/MyOwn3D";
import Contact from "./pages/Contact";

function App() {
  const history = createBrowserHistory({ window });
  return (
    <div className="App">
      <BrowserRouter>
        <Routes>
          <Route path={`${process.env.REACT_APP_PATH_PREFIX}`} element={<Home history={history} />} />
          <Route path={`${process.env.REACT_APP_PATH_PREFIX}/pages/my-avatar`} element={<MyAvatar history={history} />} />
          <Route path={`${process.env.REACT_APP_PATH_PREFIX}/pages/own-3d`} element={<MyOwn3D history={history} />} />
          <Route path={`${process.env.REACT_APP_PATH_PREFIX}/pages/contact`} element={<Contact history={history} />} />
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;

