import React, { useState, useEffect } from "react";

export default function Hero() {
  const [img, setImg] = useState("laptop");
  const [res, setRes] = useState<
    { id: string; color: string; urls: { small: string } }[]
  >([]);

  const fetchRequest = async () => {
    const data = await fetch(
      `${process.env.REACT_APP_PATH_UNSPALSH}/search/photos?page=1&query=${img}&client_id=${process.env.REACT_APP_UNSPLASH_ACCESS_KEY}`
    );
    const dataJ = await data.json();
    const result = dataJ.results;
    console.log(result);
    setRes(result);
  };

  useEffect(() => {
    fetchRequest();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <section>
      {res.map((val) => {
        return (
          <>
            <img key={val.id} src={val.urls.small} alt="" className="w-full" />
          </>
        );
      })}
    </section>
  );
}
